import React from 'react';
import { navigate } from 'gatsby';

import Button from './Button';
import GetDemoButton from './GetDemoButton';

import { screen } from '../constants';

export default function BlockText({
  pretitle = '',
  title = '',
  desc = '',
  primaryButtonTo = '/',
  primaryButtonText = '',
  secondaryButtonTo = '/',
  secondaryButtonText = '',
}) {
  function handleSecondaryButtonTextClick() {
    navigate(secondaryButtonTo);
  }

  return (
    <section className="block-text">
      <div>
        {pretitle && <div className="app__pretitle">{pretitle}</div>}
        {title && <h1 className="app__title">{title}</h1>}
        <div className="app__desc">{desc}</div>
        <div className="app__actions">
          {primaryButtonText && <GetDemoButton text={primaryButtonText} />}
          {secondaryButtonText && (
            <Button large onClick={handleSecondaryButtonTextClick}>
              {secondaryButtonText}
            </Button>
          )}
        </div>
      </div>
      <style jsx>{`
        .block-text {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          align-items: flex-start;
          text-align: center;
        }
        .block-text > div {
          flex: 1;
        }
        .block-text .app__desc,
        .block-text .app__title {
          max-width: 800px;
          margin-left: auto;
          margin-right: auto;
          margin-bottom: 1rem;
        }

        .block-text .app__desc {
          font-size: 1.25rem;
          line-height: 1.6em;
          margin-bottom: 1rem;
          margin-block-end: 1rem;
        }

        .block-text .app__desc p {
          margin-bottom: 1rem;
          margin-block-start: 1rem;
          margin-block-end: 1rem;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-text > div {
            flex: initial;
            margin: 0;
          }
          .block-text {
            flex-direction: column-reverse;
          }
          .app__title {
            line-height: 1.35;
          }
        }
      `}</style>
      <style jsx global>{`
        .block-text .app__desc p {
          margin-bottom: 1rem;
        }
      `}</style>
    </section>
  );
}
